<template>
	<div class="rcs_cont_bundle">
		<template v-if="readonly">
			<div></div>
		</template>
		<template v-else>
			<span class="custom_textarea">
				<textarea v-model="descriptionContent.inputText" id="descriptionContentArea" ref="descriptionContentArea"
					:placeholder="placeholder" :disabled="disabled"></textarea>
			</span>
			<p v-if="descriptionContent.inputText != null" class="caution text_desc">  (0 /90)</p>
			<p class="caution text_desc" v-else>  (0 /90)</p>
		</template>
	</div>
</template>

<script>

export default {
	name: 'descContentsMaker',
	props: {
		readonly: Boolean,
		disabled: Boolean,
		descriptionContent: Object,
	},
	data() {
		return {
			blank: '{{ }}',
			name: '{{name}}',
			date: '{{date}}',
			placeholder:
				'    {{ }}  .\n , ,  .(, ,   )\n)    : {{name}}, {{date}} *      90   ',
		};
	},
	mounted() {

	},
	components: {

	},
	methods: {
		toBack() {
			this.$router.push({ path: '', name: 'member' });
		},
	}
};
</script>