					<div class="emulator_cont talk" v-if="tabIndex >= 0 && talkChked"></div>
					<!-- RCS,  -->
					<div class="emulator_cont" v-else>
						<p class="ad_top_text">
							<span v-if="msgType == ''">{{ adHeadTit }}</span>
