						<div class="emulator_view">

							<div class="desc_area">
								<div class="scroll_area">
									<template>
										<p class="text">  </p>
									</template>
								</div>

								<div class="btn_wrap" v-if="buttonCount > 0">
									<span class="btn" v-if="buttonCount >= 1"> 1</span>
									<span class="btn" v-if="buttonCount >= 2"> 2</span>
									<span class="btn" v-if="buttonCount >= 3"> 3</span>
								</div>

							</div>

						</div>

						<p class="ad_bottom_text" v-if="msgType == ''"><span>  : </span></p>
					</div>

				</div>

				<!--  -   -->
				<div class="emulator_area rcs_temp" v-if="viewType == ''">
					<!--  -->
					<div class="emulator_cont talk" v-if="tabIndex2 >= 0 && talkChked"></div>
					<!-- RCS,  -->
					<div class="emulator_cont" v-else>
						<p class="ad_top_text">
							<span v-if="msgType == ''">{{ adHeadTit }}</span>
							<span v-if="msgType == ''">{{ adHeadTit2 }}</span>
						</p>

