							<div class="desc_area">
								<div class="scroll_area">
									<template>
										<p class="text">  </p>
									</template>
								</div>

								<div class="btn_wrap" v-if="buttonCount > 0">
									<span class="btn" v-if="buttonCount >= 1"> 1</span>
									<span class="btn" v-if="buttonCount >= 2"> 2</span>
									<span class="btn" v-if="buttonCount >= 3"> 3</span>
								</div>

							</div>

						</div>

						<p class="ad_bottom_text" v-if="msgType == ''"><span>  : </span></p>
					</div>

				</div>

			</div>
		</div>
	</section>
</template>

<script>
import { chkPattern } from '@/common/vue-mixins';

export default {
	name: 'emulatorViewBox',
	props: {
		adTitle: {
			type: Boolean,
			default: false,
		},
		msgType: {
			type: String,
			default: '',
