		},
		tabIndex2: {
			type: Number,
			default: -1,
		},
		tabIndexDetail: {
			type: Number,
			default: 0,
		},
		talkChked: {
			type: Boolean,
			default: false,
		},
		viewType: {
			type: String,
			default: '',
		}
	},
	mixins: [chkPattern],
	data() {
		return {
			// image: require('@/assets/images/icon/icon_type_1.png'),
			adHeadTit: '[WEB]',
			adHeadTit2: '[WEB] ()',
		};
	},
	components: {

	},
	mounted() {

	},
	methods: {

	},
	computed: {

	}
};
</script>