import ItgSendList from '../views/ItgSendList';
import ItgSendInfo from '../views/ItgSendInfo';

export default [
  //    
  {
    path: '/view/send/sms',
    name: 'smsSendPage',
    component: () => import('@/modules/sendMsg/views/SmsSendPage.vue'),
  },
  //  RCS  
  {
    path: '/view/send/rcs',
    name: 'rcsSendPage',
    component: () => import('@/modules/sendMsg/views/RcsSendPage.vue'),
  },
  //    
  {
    path: '/view/send/itg',
    name: 'itgSendPageList',
    component: () => import('@/modules/sendMsg/views/ItgSendMain.vue'),
    children: [
      //    - 
      {
        path: '',
        name: 'itgSendList',
        component: ItgSendList,
      },
      //    - 
      {
        path: 'new',
        name: 'itgSendNew',
        component: ItgSendInfo,
      },
      //    - 
      {
        path: 'info/:userId',
        name: 'itgSendInfo',
        component: ItgSendInfo,
      },
    ],
  },
  //   
  {
    path: '/view/send/req',
    name: 'reqSendList',
    component: () => import('@/modules/sendMsg/views/ReqSendList.vue'),
  },
];
